.hero-section {
  padding: 50px 10%;
  @media (max-width: 868px) {
    padding: 20px;
  }
  .left {
    width: 50%;
    @media (max-width: 868px) {
      width: 100%;
      text-align: center;
      padding: 50px 0;
    }
    h6 {
      font-size: 27px;
      color: goldenrod;
    }
    h1 {
      font-size: 4.5em;
      font-weight: bold;
      text-transform: capitalize;
      margin: 20px 0;
      @media (max-width: 468px) {
        font-size: 3em;
      }
    }
    h4 {
      font-size: 2rem;
      color: goldenrod;
      @media (max-width: 468px) {
        font-size: 1.4em;
      }
    }
    p {
      font-size: 1rem;
      margin: 10px 0;
      font-weight: 400;
    }
    .golden {
      padding: 20px;
      border: solid 2px goldenrod;
      font-weight: bold;
      font-size: 1.2em;
      background-color: goldenrod;
      margin: 20px 0;
      cursor: pointer;
      color: inherit;
      border-radius: 50px;
      &:hover {
        background-color: inherit;
      }
      @media (max-width: 420px) {
        padding: 10px;
        font-size: 1em;
      }
      @media (max-width: 320px) {
        margin: 10px 0;
      }
      a{
        color: inherit;
        text-decoration: none;
      }
    }
    .gold {

      padding: 20px;
      border: solid 2px goldenrod;
      font-weight: bold;
      font-size: 1.2em;
      background-color: inherit;
      margin: 20px 0;
      margin-left: 20px;
      color: inherit;
      cursor: pointer;
      border-radius: 50px;
      &:hover {
        background-color: inherit;
        color: inherit;
      }
      @media (max-width: 420px) {
        padding: 10px;
        font-size: 1em;
      }
      @media (max-width: 320px) {
        margin: 10px 0;
      }
      a{
        color: inherit;
        text-decoration: none;
      }
    }
  }

  .right {
    width: 45%;
    @media (max-width: 868px) {
      display: none;
    }
    img {
      width: 90%;
    }
  }
}
