.contact-section {
  padding: 10%;
  @media (max-width: 900px) {
    padding: 10px;
  }
  .txt {
    width: 50%;
    margin: 20px auto;
    text-align: center;
    @media (max-width: 700px) {
      width: 100%;
    }
  }
  .conta-section {
    margin: 40px 0;

    @media (max-width: 868px) {
      flex-direction: column;
    }
    .contacts {
      align-items: flex-start;
      @media (max-width: 668px) {
       width: 100%;
      }
      .conts {
        align-items: flex-start;
        @media (max-width: 868px) {
          flex-direction: row;
        }
        @media (max-width: 668px) {
          flex-direction: column;
        }
        .contact {
          @media (max-width: 868px) {
            flex-direction: column;
            margin: 20px;
            text-align: center;
          }
          @media (max-width: 668px) {
            flex-direction: row;
            // margin: 20px;
            text-align: left;
          }
          .icon {
            border: solid 2px gold;
            padding: 10px;
            color: inherit;
            margin: 10px 0;
            margin-right: 20px;
            @media (max-width: 868px) {
              margin: 0;
            }
            @media (max-width: 668px) {
              margin-right: 20px;
            }
          }
          .text {
            h2 {
              font-weight: 400;
              font-size: 1em;
            }
            h4 {
              font-size: 1.5rem;
              font-weight: 500;
              @media (max-width: 900px) {
                font-size: 1.1em;
              }
            }
          }
        }
      }
      .social {
        margin: 40px 0;
        @media (max-width: 868px) {
            width: 100%;
            text-align: center;
          }
        h1 {
          text-transform: capitalize;
          margin: 10px 0;
        }
        ul {
          li {
            a{
            border: solid 2px gold;
            padding: 10px;
            color: inherit;
            margin: 10px 0;
            margin-right: 20px;
            font-size: 1.3em;
            &:hover {
              background-color: gold;
              cursor: pointer;
            }
          }}
        }
      }
    }
    .form-control {
      width: 60%;
      padding: 20px;
      background-color: rgba(238, 238, 238, 0.129);
      @media (max-width: 868px) {
        width: 100%;
      }
      input,
      select,
      textarea,
      .submit,option {
        padding: 10px;
        width: 100%;
        margin: 12px 0;
        outline: none;
        border: 0;
        border-radius: 5px;
        background-color: rgba(204, 204, 204, 0.33);
        &::placeholder {
          color: inherit;
        }
        &:focus {
          border-bottom: 2px solid gold;
        }
      }
      .submit {
        background-color: gold;
        color: black;
        font-size: 1em;
        font-weight: bold;
        cursor: pointer;
      }
    }
  }
}
