.navbar {
  padding: 10px 10%;
  width: 100%;
  background-color: inherit;
  position: sticky;
  left: 0;
  top: 0;
  z-index: 9900;
  @media (max-width: 868px) {
    padding: 10px;
  }
  .logo {
    width: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    padding: 10px 1px;
    border: solid 2px gold;
    border-radius: 42%;
    img {
      width: 60%;
      margin: 0 10px;
      @media (max-width: 800px) {
        width: 40%;
      }
    }
  }
  .navbar-nav {
    list-style: none;
    @media (max-width: 868px) {
      display: none;
    }
    .nav-item {
      a {
        color: inherit;
        text-decoration: none;
        font-size: 20px;
        font-weight: 500;
        text-transform: capitalize;
        margin: 0 20px;
        &:hover {
          color: goldenrod;
        }
      }
    }
  }

  .navbar-mobile {
    list-style: none;
    display: none;
    @media (max-width: 868px) {
      width: 100%;
      min-height: 100%;
      height: 80vh;
      background-color: inherit;
      padding: 120px 20px;
      display: flex;
      position: absolute;
      left: 0;
      top: 60px;
      z-index: 20;
      transition: .4s;
    }
    .nav-item {
      a {
        color: inherit;
        text-decoration: none;
        font-size: 20px;
        font-weight: 500;
        text-transform: capitalize;
        margin: 20px 0;
        align-items: flex-start;
        &:hover {
          color: goldenrod;
        }
      }
    }
  }
  .off {
    // display: block;
    @media (max-width: 868px) {
      left: -100%;
    }
  }
  .set {
    width: 70px;
    align-items: center;
    .navbar-toggler {
      display: none;
      @media (max-width: 868px) {
        display: block;
        font-size: 2em;
      }
    }
  }
}
