.about-section {
  padding: 10%;
  background-color: rgba(163, 163, 163, 0.023);
  @media (max-width: 868px) {
    padding: 20px;
  }
  .center {
    width: auto;
    margin: -20% auto 150px auto;
    padding: 20px;
    background-color: goldenrod;
    box-shadow: 2px 2px 20px 2px rgba(4, 4, 4, 0.3);
    border-radius: 100px;
    @media (max-width: 868px) {
      padding: 10px;
      position: relative;
      margin: 10px auto;
    }
    @media (max-width: 468px) {
      border-radius: 0;
    }
    .comps {
      padding: 10px;
      @media (max-width: 468px) {
        flex-direction: column;
      }
      li {
        margin: 0 20px;
        @media (max-width: 768px) {
          flex-direction: column;
          text-align: center;
          margin: 0;
        }
        @media (max-width: 468px) {
          flex-direction: row;
          text-align: left;
          align-items: flex-start;
        }
        h3 {
          padding: 20px;
          font-size: 3em;
          background-color: goldenrod;
          box-shadow: 2px 2px 20px 2px rgba(4, 4, 4, 0.3);
          border-radius: 50%;
          @media (max-width: 868px) {
            padding: 10px;
            font-size: 1.5em;
          }
        }
        .comp {
          margin: 10px;
          color: black;
          h2 {
            @media (max-width: 568px) {
              font-size: 20px;
            }
          }
          h5 {
            font-size: 20px;
            font-weight: 500;
            @media (max-width: 868px) {
              font-size: 16px;
            }
          }
        }
      }
    }
  }
  .bottom {
    @media (max-width:868px) {
      flex-direction: column;
    }
    .left {
      width: 35%;
      @media (max-width:868px) {
        width: 100%;
      }
      .class {
        width: 100%;
        height: 400px;
        position: relative;
        background-color: goldenrod;
        border: solid 10px goldenrod;
        @media (max-width:868px) {
          height: 600px;
          margin-top: 100px;
        }
      }
      .class::before {
        content: "";
        position: absolute;
        right: 30px;
        left: -30px;
        bottom: 30px;
        width: 100%;
        height: 100%;
        background-image: url(../../Img/shafiq.png);
        background-size: cover;
      }
    }

    .right {
      width: 50%;
      margin-left: 40px;
      @media (max-width:868px) {
        width: 100%;
        margin: 10px;
      }
      span{
        color: goldenrod;
        
      }
      h1{
        font-size: 3.5rem;
        @media (max-width:368px) {
          font-size: 2em;
        }
      }
      p{
        margin: 10px 0;
      }
      .grid-items {
        text-align: left;
        margin: 20px 0;
        li {
          display: grid;
          grid-template-columns: 30% 15% 50%;
          gap: 20px;
          justify-items: start;
          @media (max-width:868px) {
            grid-template-columns: 25% 5% 70%;
          }
          h3 {
            display: flex;
            align-items: center;
            justify-content: space-between;
            font-weight: 500;
            span {
              color: goldenrod;
              margin: 10px;
            }
          }
        }
      }

      .interest {
        .int {
          h3 {
            display: flex;
            align-items: center;
            justify-content: space-between;
            font-weight: 500;
            span {
              color: goldenrod;
              margin: 10px;
            }
          }
        }
      }
    }
  }
}
