.skills {
  padding: 10%;
  .top {
    width: 50%;
    margin: 20px auto;
    text-align: center;
    @media (max-width: 868px) {
      width: 100%;
      text-align: center;
      padding: 50px 0;
    }

    h6 {
      font-size: 27px;
      color: goldenrod;
    }
    h1 {
      font-size: 3.5em;
      font-weight: bold;
      text-transform: capitalize;
      margin: 20px 0;
      @media (max-width: 468px) {
        font-size: 2em;
      }
    }

    p {
      font-size: 1rem;
      margin: 10px 0;
      font-weight: 400;
    }
  }
  .serves {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    word-wrap: break-word;
    gap: 20px;
    margin: 5% 0;
    @media (max-width: 768px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media (max-width: 568px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  .skill{
    text-align: center;
    .icon{
      margin: 10px auto;
      font-size: 3em;
      color: gold;
      padding: 10px;
      display: flex;
      align-items: center;;
      justify-content: center;
      border: solid gold 2px;
      border-radius: 100%;
    }
    h1{
      font-size: 2em;
      text-transform: capitalize;
    }
  }
}
